.block {
  background: -webkit-gradient(linear, left top, right top, from(#276caf), to(#30a1c1));
  background: -webkit-linear-gradient(left, #276caf, #30a1c1);
  background: -o-linear-gradient(left, #276caf, #30a1c1);
  background: linear-gradient(to right, #276caf, #30a1c1);
  height: 25rem;
  z-index: 1; }
  @media (max-width: 767px) {
    .block {
      height: 50vh; } }

